<template>
    <transition>
        <div id="me">
            <Tile :data="introduction"></Tile>
        </div>
    </transition>
</template>

<script>
    import Tile from "../components/Tile";
    export default {
        name: "Me",
        components: {Tile},
        data: function () {
            return {
                introduction: [
                    {
                        title: 'Basic Info',
                        options: 'WITH_LABEL',
                        contents: [
                            {
                                label: '',
                                value: ' ',
                                type: 'text'
                            },
                            {
                                label: '',
                                value: '1994/12/27',
                                type: 'text'
                            },
                            {
                                label: '',
                                value: '',
                                type: 'text'
                            },
                            {
                                label: 'Facebook',
                                value: 'https://www.facebook.com/kennytakimura',
                                url: 'https://www.facebook.com/kennytakimura',
                                type: 'url'
                            },
                            {
                                label: 'Twitter',
                                value: 'Kenny',
                                url: 'https://twitter.com/kenta_takimura',
                                type: 'url'
                            },
                            {
                                label: 'LinkedIn',
                                value: 'Kenta Takimura',
                                url: 'https://www.linkedin.com/in/kenta-takimura/',
                                type: 'url'
                            },
                            {
                                label: 'Github',
                                value: 'kennyTakimura',
                                url: 'https://github.com/kennyTakimura',
                                type: 'url'
                            },
                            {
                                label: 'Blog',
                                value: 'kenny27',
                                url: 'http://kenny27.hatenablog.com/',
                                type: 'url'
                            },
                            {
                                label: 'Notes',
                                value: 'Really like learning new skills, always pursuing better environment.',
                                type: 'text'
                            }
                        ]
                    },
                    {
                        title: 'Biography',
                        options: 'WITH_LABEL',
                        contents: [
                            {
                                label: '20173',
                                value: '',
                                type: 'text'
                            },
                            {
                                label: '20174',
                                value: 'IT',
                                type: 'text'
                            }
                        ]
                    }
                ]
            }
        }
    }
</script>

<style scoped>
    #me {
        width: 100%;
        padding-top: 20px;
        padding-left: 15px;
        background-color: #000000;
    }
    .v-enter-active {
        transition: .5s;
    }
    .v-enter {
        transform: translate(0, 100%);
    }
    .v-leave-to {
        transform: translate(0, -100%);
    }
</style>



